<template>
<div class="row mt-5" @mouseover="validate" >

<div  class="col-sm-3 myH shadow pt-2">
<form>
  <div class="form-group ">
    
    <input v-model="name" type="text" class="form-control" placeholder="Full Name" id="exampleInputEmail1" required aria-describedby="emailHelp">
    
  </div>
  <div class="form-group mt-5">
    
    <input v-model="email" placeholder="E-mail" type="email" class="form-control" id="email" required>
  </div>
  <div class="form-group mt-5">
    
    <input v-model="phone" type="phone" placeholder="Phone Number" class="form-control" id="phone" required>
  </div>
  <div class="form-group mt-5">
    
    <input v-model="userName" placeholder="User Name" type="text" class="form-control" id="userName" required>
  </div>
 
</form>
 <button  type="submit" :disabled="validates" @click="addUser()" class="btn btn-primary ml-5 mt-4">ADD USER</button>
</div>
<div class="col-sm-8 offset-md-1">
  <div class="d-flex align-items-center text-success" v-if="status">
  <strong>Loading...</strong>
  <div class="spinner-border ml-auto" role="status" aria-hidden="true"></div>
</div>
    <table class=" table table-striped  table-hover text-center">
  <thead>
    <tr>
      <th scope="col">NAME</th>
      <th scope="col">EMAIL</th>
      <th scope="col">PHONE</th>
      <th scope="col">USER NAME</th>
      <th scope="col">...</th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="(user, index) in users" :key="user.id">
      <td>{{user.name}}</td>
      <td>{{user.email}}</td>
      <td>{{user.phone}}</td>
      <td>{{user.username}}</td>
      <td> <Span class="btn btn-danger" @click="deleteUser(index)"> Remove</Span></td>
    </tr>
  </tbody>
</table>
</div>
</div>
</template>

<script>
import axios from "axios";
export default {
    data(){
        return{
            users:[],
            name: null,
            email: null,
            phone: null,
            userName: null,
            status: false,
            validates: false

        }

    },
    methods: {
         addUser: function(){
           
           this.status=true;
            axios.post('https://jsonplaceholder.typicode.com/users',{name:this.name, username:this.userName,email:this.email, phone:this.phone}) .then(res=>{
          
              this.users.push(res.data);
              this.name='';
              this.email='';
              this.phone='';
              this.userName='';
              
              this.status=false;


              
            })
        },
        validate: function(){
             if(this.name && this.email && this.phone && this.userName){
                  this.validates=false;
             }else{
               this.validates=true;
             }
        },
        
       deleteUser: function (user){
              this.status=true;
          axios.delete(`https://jsonplaceholder.typicode.com/users/${user}`).then(res=>{
            
            this.users.splice(user,1);
            res.data=null
            this.status=false;
          })

            
        }
    },
    async created(){
      this.status=true;
        let getUsers= await axios.get('https://jsonplaceholder.typicode.com/users');

        this.users= getUsers.data;
            this.status=false;

    }
    
}
</script>

<style scoped>
 .myH{
   height: 450px;
 }
</style>